<template>
  <div>
    <span v-if="isCmsPage" v-html="data.data.content" />
    <div v-else-if="viewData">
      <ViewProduct
        v-if="viewAlias === 'Product'"
        :path="path"
        :initialData="viewData"
      />
      <ViewRestaurant
        v-if="viewAlias === 'Restaurant'"
        :initialData="viewData"
      />
      <ViewProducts
        v-if="viewAlias === 'Store'"
        :path="path"
        :initialData="viewData"
      />
      <ViewGroceries v-if="viewAlias === 'Groceries'" :storeUrl="path" />
      <ViewDeal
        v-if="viewAlias === 'Deal'"
        :path="path"
        :initialData="viewData"
      />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import ViewProduct from "@/views/ViewProduct";
import ViewProducts from "@/views/ViewProducts";
import ViewGroceries from "@/views/ViewGroceries";
import ViewDeal from "@/views/ViewDeal";
import ViewRestaurant from "@/views/ViewRestaurant";
import get from "@/util/get";
import getPathData from "@/mixins/getPathData";

export default {
  name: "ViewCmsPage",
  mixins: [getPathData],
  components: {
    ViewProduct,
    ViewProducts,
    ViewGroceries,
    ViewRestaurant,
    ViewDeal,
  },
  props: {
    path: String,
  },
  data() {
    return {
      isCmsPage: false,
      data: undefined,
      viewAlias: undefined,
      viewData: undefined,
    };
  },
  methods: {
    get(...params) {
      return get(...params);
    },
    async getAndDetectPathData() {
      const data = await this.getPathData(this.path);
      const dataInArr = Object.values(data);
      const dataView = get(dataInArr, `${dataInArr.length - 1}.0.view`);
      // const currView = this.$router.history.current.name;

      this.viewAlias = dataView;
      this.viewData = data;
    },
    async fetchCMSPage() {
      const { data } = await axios.get(`/api/rest/cms/page_id/${this.path}`);

      this.isCmsPage = Boolean(
        data.status === "success" && +data.data.is_active
      );
      this.data = data;
    },
    async init() {
      this.isCmsPage = false;
      this.viewData = undefined;
      this.viewAlias = undefined;

      this.fetchCMSPage();
      this.getAndDetectPathData();
    },
  },
  mounted() {
    this.init();
  },
  watch: {
    $route() {
      this.init();
    },
  },
};
</script>
