<template>
  <div class="image-gallery row">
    <div class="col-sm-12 col-lg-7 col-lg-push-1 image-gallery__mainImg">
      <SwipperButtons :onSwipeClick="updateElementWidth" ref="swiperBtn">
        <DraggableScroll
          class="image-gallery__img-slider-container hide-scroll-bar"
        >
          <div
            class="image-gallery__slider-item"
            v-for="(image, index) in images"
            :key="image.bigImgURL"
            :ref="`sliderItem${index}`"
          >
            <div class="image-gallery__img-wrapper" v-if="dataReady">
              <ImageViewer
                :src="image.bigImgURL"
                class="img"
                :alt="productName"
                :id="`image-gallery__slider-item--${index}`"
              />
            </div>
            <div class="image-gallery__img-wrapper app-ph" v-else />
          </div>
        </DraggableScroll>
      </SwipperButtons>
    </div>
    <div class="col-sm-12 col-lg-1 col-lg-pull-7">
      <div class="row gallery" v-if="dataReady">
        <div
          v-for="(image, index) in images"
          :key="image.smallImgURL"
          class="col-xs-2 col-lg-12 galleryimage"
        >
          <ImageViewer
            :src="image.smallImgURL"
            class="img"
            @click="switchImage(index)"
            :alt="productName"
          />
        </div>
      </div>
      <div class="row gallery gallery__app-ph" v-else>
        <div v-for="item in [1, 2, 3]" :key="item" class="app-ph" />
      </div>
    </div>
  </div>
</template>

<script>
import get from "@/util/get";
import ImageViewer from "@/components/ImageViewer";
import DraggableScroll from "@/components/DraggableScroll";
import SwipperButtons from "@/components/SwipperButtons";

export default {
  components: {
    ImageViewer,
    DraggableScroll,
    SwipperButtons,
  },
  props: {
    productName: String,
    dataReady: {
      type: Boolean,
      default: true,
    },
    images: Array,
  },
  data() {
    return {
      selectedImgIndex: 0,
    };
  },
  computed: {
    selectedImg() {
      const { images, selectedImgIndex } = this;
      return get(images, `${selectedImgIndex}.bigImgURL`, "");
    },
  },
  methods: {
    updateElementWidth() {
      const element = this.$refs.sliderItem0;
      return {
        skipWidth: element ? element[0].getBoundingClientRect().width : 0,
      };
    },
    switchImage(imgIndex) {
      this.selectedImgIndex = imgIndex;
      const activeElement = document.getElementById(
        `image-gallery__slider-item--${imgIndex}`
      );
      if (!activeElement) return;
      activeElement.scrollIntoView(false);
    },
  },
  mounted() {
    this.$refs.swiperBtn.updateCarouselArrowIndicator();
  },
  watch: {
    dataReady() {
      // const self = this;
      setTimeout(() => this.$refs.swiperBtn.updateCarouselArrowIndicator());
    },
  },
};
</script>

