<script>
import BaseButton from "@/components/BaseButton";
import CartItemMixin from "@/mixins/CartItemMixin";
import getPathData from "@/mixins/getPathData";
import get from "@/util/get";
import { mapGetters, mapActions } from "vuex";
import { MODAL_KEY_SELECT_DELIVERY_OPTION } from "@/store/modalModule";
import { LOADING_KEY_FETCHING_DATA } from "@/store/loadingModule";
import { SELLER_TYPES } from "@/util/constants";
import DeliveryBarWrapper from "@/components/DeliveryBarWrapper";
import DeliveryCurrAddressContainer from "@/components/DeliveryCurrAddressContainer";
import ImageViewer from "@/components/ImageViewer";
import ImageGallery from "@/components/ImageGallery";

export default {
  name: "ViewProduct",
  mixins: [CartItemMixin, getPathData],
  components: {
    BaseButton,
    DeliveryBarWrapper,
    DeliveryCurrAddressContainer,
    ImageViewer,
    ImageGallery,
  },
  props: {
    path: String,
    initialData: Object,
  },
  data() {
    return {
      product: {},
      parent: {},
      images: [],
      dataReady: false,
      showMore: false,
      isOverflowing: false,
    };
  },
  computed: {
    ...mapGetters(["currentNettoStoreIdByMethod", "getNettoURLByID"]),
    imagesForGallery() {
      return Object.values(this.images).map(({ value }) => ({
        bigImgURL: this.buildImgUrl({ src: value, size: "big" }),
        smallImgURL: this.buildImgUrl({ src: value, size: "small" }),
      }));
    },
    mainImage() {
      if (!this.product.image || this.product.image === "no_selection")
        return "";
      return (
        "https://images.aha.is/image/upload/f_auto,w_1480/products/" +
        this.product.image
      );
    },
    parentLogo() {
      if (!this.parent.thumbnail || this.parent.thumbnail === "no_selection")
        return "";
      return (
        "https://images.aha.is/image/upload/f_auto,w_80/products/" +
        this.parent.thumbnail
      );
    },
    storeURL() {
      const { getNettoURLByID, parent } = this;
      return getNettoURLByID(parent && parent.entity_id) || "";
    },
    getPrice() {
      const { special_price, price } = this.product;
      return special_price || price;
    },
    isNetto() {
      return this.parent.is_name && this.parent.is_name.includes("Nett");
    },
  },
  methods: {
    ...mapActions(["toggleModal", "setNavBarTitle", "toggleLoading"]),
    smallImage(src) {
      if (!src || src === "no_selection") return "";
      return (
        "https://images.aha.is/image/upload/f_auto,w_100,h_100,c_fill/products/" +
        src
      );
    },
    buildImgUrl({ src, size = "small" }) {
      if (!src || src === "no_selection") return "";
      return (
        (size === "small"
          ? "https://images.aha.is/image/upload/f_auto,w_100,h_100,c_fill/products/"
          : "https://images.aha.is/image/upload/f_auto,w_1480/products/") + src
      );
    },
    switchImage(src) {
      if (!src || src === "no_selection") return;
      this.product.image = src;
    },
    fixHtmlImages(html) {
      return html.replace(
        "https:/media/wysiwyg/",
        "https://cdn.aha.is/media/wysiwyg/"
      );
    },
    findStoreData({ currStoreId, storeList }) {
      const { data } = this;
      return currStoreId
        ? storeList.find(store => store.entity_id === currStoreId)
        : get(data, "2.0");
    },
    initData(data) {
      const { currentNettoStoreIdByMethod, findStoreData, toggleModal } = this;
      this.product = data[0][0];
      this.toggleLoading({
        loadingKey: LOADING_KEY_FETCHING_DATA,
        isLoading: false,
      });
      this.setNavBarTitle({
        en: get(this.product, "en_name", ""),
        is: get(this.product, "is_name", ""),
      });
      const seller_product_links = get(data, "0.0.seller_product_links");

      let matchStoreProduct;
      if (seller_product_links) {
        if (!currentNettoStoreIdByMethod) {
          return toggleModal({
            modalKey: MODAL_KEY_SELECT_DELIVERY_OPTION,
            sellerType: SELLER_TYPES.GROCERY,
            noSuccessRedirect: true,
            isUncloseable: true,
          });
        }
        // Product of netto store only
        matchStoreProduct = JSON.parse(seller_product_links).find(
          e => e.seller_id === currentNettoStoreIdByMethod
        );

        this.product.entity_id = matchStoreProduct.product_id;
      }

      this.images = data[1];
      this.parent = findStoreData({
        currStoreId: matchStoreProduct && matchStoreProduct.seller_id,
        storeList: Object.values(get(data, "2", {})),
      });
      this.dataReady = true;
    },
  },
  created() {
    this.toggleLoading({
      loadingKey: LOADING_KEY_FETCHING_DATA,
      isLoading: true,
    });
  },
  async mounted() {
    const { initData } = this;
    this.data = this.initialData || (await this.getPathData(this.path));
    initData(this.data);
  },
  updated() {
    if (!this.dataReady) return;
    let element = this.$refs.prodDescription;
    this.isOverflowing = element.offsetHeight >= 214;
  },
  watch: {
    currentNettoStoreIdByMethod() {
      this.initData(this.data);
    },
  },
};
</script>

